* {
  padding: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: "Avenir";
  font-weight: 300;
  font-style: normal;
  src: url("./fonts/Avenir-Book.ttf") format("truetype");
}

@font-face {
  font-family: "Avenir";
  font-weight: 600;
  font-style: normal;
  src: url("./fonts/Avenir-Heavy.ttf") format("truetype");
}

body {
  font-family: "Avenir", sans-serif;
  font-size: 16px;
}

.searchForm {
  display: flex;
  justify-content: center;
  margin: 20px auto 0;
}

.searchFormBig {
  width: 100%;
  height: 70px;
}

.searchFormSmall {
  width: 100%;
  height: 42px;
}

.label {
  position: relative;
  display: block;
  width: 100%;
}

.label:after {
  position: absolute;
  content: "";
  display: block;
}

.labelBig:after {
  background-image: url("Search.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 19px;
  height: 19px;
  top: 25px;
  left: 26px;
}

.labelSmall:after {
  background-image: url("Search.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 11px;
  height: 11px;
  top: 15px;
  left: 13px;
}

.input {
  width: 100%;
  height: 100%;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  color: #3d4e61;
  font-weight: 300;
  font-family: inherit;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  outline: none;
}

.input:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.inputBig {
  padding-left: 62px;
  font-size: 16px;
  line-height: 22px;
}

.inputSmall {
  padding-left: 33px;
  font-size: 14px;
  line-height: 19px;
}

.input::placeholder {
  color: inherit;
  font-weight: 300;
}

.inputBig::placeholder {
  font-size: 16px;
  line-height: 22px;
}

.inputSmall::placeholder {
  font-size: 14px;
  line-height: 22px;
}

.input:focus {
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  font-weight: 600;
}
